.App {
  text-align: center;
}

.titleapp {
  font-weight: 700;
  text-align: center;

}

.btndiv {
  display: flex;
  width: 100%;
}

.tabbutton1 {
  width: 34%;
  height: 10vh;
  border: 2px solid black;
  border-top-left-radius: 10px;
  font-weight: 700;
  color: green;
  font-size: x-large;
  /* border-bottom-right-radius: 10px; */
  border-bottom-left-radius: 10px;
}

.tabbutton1.Feed {
  background-color: black;
  color: orange;
}


.tabbutton2 {
  width: 33%;
  height: 10vh;
  border: 2px solid black;
  font-weight: 700;
  color: green;
  font-size: x-large;

  .Detail {
    background-color: black;
    color: orange;
  }

}

.tabbutton2.Detail {
  background-color: black;
  color: orange;
}

.tabbutton3 {
  width: 33%;
  height: 10vh;
  border: 2px solid black;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  font-weight: 700;
  color: green;
  font-size: x-large;

  .Archive {
    background-color: black;
    color: orange;
  }

}

.tabbutton3.Archive {
  background-color: black;
  color: orange;
}

.archbtn {
  width: 90%;
  margin-top: 5vh;
  border: 2px solid black;
  border-radius: 10px;
  padding: 2vh;
  font-weight: 700;
  color: green;
  font-size: large;
}

.datadiv {
  text-align: center;
}

.datadivd {
  text-align: center;
  font-weight: 700;
  border: 2px solid black;
  border-radius: 10px;
  width: 80%;
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
}

.calldiv {
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  width: 92%;
  margin-left: 2.5%;
  margin-bottom: 2%;
  border-radius: 10px;
}

.nodiv {
  display: flex;
  justify-content: space-around;
  color: black;
  font-weight: 900;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom: 2px solid brown;
  background-color: gray;
}

.detailbtn {
  background-color: aliceblue;
  color: green;
  border-radius: 10px;
  padding: 10px;
  font-weight: 700;
}

.detailbtn:hover {
  background-color: black;
}

.detaildiv {
  display: flex;
  justify-content: space-evenly;
  padding: 1%;
}